import { ToastService } from './../../../_services/_toast/_toast.service';
import { Component, OnInit } from '@angular/core';
import { Constants } from '../../../constants';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { DbService } from 'src/app/_services';

@Component({
  templateUrl: './add-business.component.html',
  styleUrls: ['./add-business.component.css'],
})
export class AddBusinessComponent implements OnInit {
  constructor(
    public constants: Constants,
    private formBuilder: FormBuilder,
    private db: DbService,
    private toastService: ToastService
  ) {}

  businessForm: FormGroup;

  ngOnInit() {
    this.InitForm();
  }

  private InitForm() {
    this.businessForm = this.formBuilder.group({
      name: [[], [Validators.required]],
      address: [[], [Validators.required]],
      suburb: [[], [Validators.required]],
      city: [[], [Validators.required]],
      postalCode: [[], [Validators.required]],
      invoicePrefix: [[], [Validators.required]],
      statementPrefix: [[], [Validators.required]],
      quotationPrefix: [[], [Validators.required]],
      bankName: [[], [Validators.required]],
      bankAcc: [[], [Validators.required]],
      bankBranch: [[], [Validators.required]],
      cellNumber: [[], [Validators.required]],
      alternateNumber: [[], [Validators.required]],
      email: [[], [Validators.required]],
    });
  }

  public TryAddBusiness() {
    if (this.businessForm.valid) {
      this.AddBusiness();
    } else {
      this.toastService.showError('Missing required fields');
    }
  }

  private async AddBusiness() {
    const business = this.businessForm.value;
    try {
      await this.db.addBusiness(business);
      this.toastService.showSuccess(`Business '${business.name}' added successfully`);
    } catch (error) {
      this.toastService.showError(error);
    }
  }
}
